<template>
    <div class="display-7">정보 설정
        <hr>
    </div>
    <div class="d-flex justify-content-evenly">
        <!-- <button class="btn btn-secondary" type="button" @click="circleBtn.value.style.color ='red'" ref="circleBtn"  data-bs-toggle="collapse" -->
        <button class="btn btn-secondary " type="button" data-bs-toggle="collapse" ref="circleBtn"
            @click="circleBtnSwitchColor('circleBtn')" data-bs-target="#circles" aria-expanded="true"
            aria-controls="circles">
            나의 그룹
        </button>
        <button class="btn btn-secondary" type="button" ref="goalBtn" data-bs-toggle="collapse" data-bs-target="#goals"
            @click="circleBtnSwitchColor('goalBtn')" aria-expanded="false" aria-controls="goals">
            나의 목표
        </button>
        <button class="btn btn-secondary" type="button" data-bs-toggle="collapse" data-bs-target="#questions"
            @click="circleBtnSwitchColor('questionBtn')" ref="questionBtn" aria-expanded="false"
            aria-controls="questions">
            나의 질문
        </button>
        <button class="btn btn-secondary" type="button" data-bs-toggle="collapse" data-bs-target="#answers"
            @click="circleBtnSwitchColor('answerBtn')" ref="answerBtn" aria-expanded="false" aria-controls="answers">
            나의 답
        </button>
        <button class="btn btn-secondary" type="button" data-bs-toggle="collapse" data-bs-target="#comments"
            @click="circleBtnSwitchColor('commentBtn')" ref="commentBtn" aria-expanded="false" aria-controls="comments">
            나의 댓글
        </button>
    </div>
    <br>
    <div class="collapse" id="circles">
        <div class="card card-body">
            <CircleListByUser />
        </div>
    </div>
    <div class="collapse" id="goals">
        <div class="card card-body">
            <GoalListForPrivate />
        </div>
    </div>
    <div class="collapse" id="questions">
        <div class="card card-body">
            <PrivateQuestionByEmail />
        </div>
    </div>
    <div class="collapse" id="answers">
        <div class="card card-body">
            <PrivateAnswerByEmail />
        </div>
    </div>
    <div class="collapse" id="comments">
        <div class="card card-body">
            <PrivateCommentByEmail />
        </div>
    </div>
    <!-- <router-link tag="a" to="/privateQbyemail">페이지로 이동</router-link> -->
</template>

<script>
import { ref } from 'vue'
import PrivateAnswerByEmail from './PrivateAnswerByEmail.vue';
import PrivateCommentByEmail from './PrivateCommentByEmail.vue';
import PrivateQuestionByEmail from './PrivateQuestionByEmail.vue';
import GoalListForPrivate from './GoalListForPrivate.vue';
import CircleListByUser from './CircleListByUser.vue';

export default {
    components: {
        CircleListByUser, GoalListForPrivate, PrivateAnswerByEmail, PrivateCommentByEmail, PrivateQuestionByEmail
    },
    setup() {
        const circleBtn = ref('');
        const questionBtn = ref('');
        const commentBtn = ref('');
        const answerBtn = ref('');
        const goalBtn = ref('');
        const circleBtnSwitchColor = (btn) => {

            if (btn == 'circleBtn') {
                if (circleBtn.value.classList.contains("btn-secondary")) {
                    circleBtn.value.classList.remove("btn-secondary")
                    circleBtn.value.classList.add("btn-info");
                } else {
                    circleBtn.value.classList.remove("btn-info")
                    circleBtn.value.classList.add("btn-secondary");
                }
            }
            if (btn == 'questionBtn') {
                if (questionBtn.value.classList.contains("btn-secondary")) {
                    questionBtn.value.classList.remove("btn-secondary")
                    questionBtn.value.classList.add("btn-info");
                } else {
                    questionBtn.value.classList.remove("btn-info")
                    questionBtn.value.classList.add("btn-secondary");
                }
            }
            if (btn == 'commentBtn') {
                if (commentBtn.value.classList.contains("btn-secondary")) {
                    commentBtn.value.classList.remove("btn-secondary")
                    commentBtn.value.classList.add("btn-info");
                } else {
                    commentBtn.value.classList.remove("btn-info")
                    commentBtn.value.classList.add("btn-secondary");
                }
            }
            if (btn == 'answerBtn') {
                if (answerBtn.value.classList.contains("btn-secondary")) {
                    answerBtn.value.classList.remove("btn-secondary")
                    answerBtn.value.classList.add("btn-info");
                } else {
                    answerBtn.value.classList.remove("btn-info")
                    answerBtn.value.classList.add("btn-secondary");
                }
            }
            if (btn == 'goalBtn') {
                if (goalBtn.value.classList.contains("btn-secondary")) {
                    goalBtn.value.classList.remove("btn-secondary")
                    goalBtn.value.classList.add("btn-info");
                } else {
                    goalBtn.value.classList.remove("btn-info")
                    goalBtn.value.classList.add("btn-secondary");
                }
            }

        }

        return {
            circleBtn, circleBtnSwitchColor, questionBtn
            , commentBtn
            , answerBtn
            , goalBtn
        }
    }


}
</script>

