<template>
  <div class="container text-center">
    <div class="row  container ">
      <!-- left column -->
      <div class="col-lg-6 col-sm-12 container">
        <div class="row g-2">

          
          <div class="col-lg-3 col-sm-3 lowerAvatarPic">
            <img v-if="!(state.urltest==null)" v-bind:src="state.img" class="img-thumbnail rounded-circle  "
              alt="대표이미지" />
            <img v-else="state.urltest==null" src="../assets/images/pictures/profile_icon.png"
              class="img-thumbnail rounded-circle  " alt="대표이미지">
            <UserProfilePic />
            <div class="box"></div>
          </div>


          <div class="col-lg-9 col-sm-9">
            <div class="row">
              <th class="col-9">내 정보</th>
              <th class="col-3">
                <h6>공개 여부</h6>
              </th>
            </div>
            <div class="row">
              <div class="col-9">
                <div class="input-group mb-2  ">
                  <span class="input-group-text labelLength6" id="basic-addon1">이메일</span>
                  <input type="text" class="form-control" v-model="state.email" disabled>
                </div>
              </div>
              <div class="col-3">
                <el-switch v-model="swemail" size="large" />
              </div>
            </div>
            <div class="row">
              <div class="col-9">
                <div class="input-group mb-2 ">
                  <span class="input-group-text labelLength6" id="basic-addon1">닉네임</span>
                  <input type="text" class="form-control" v-model="state.nickname" ref="nicknamee">
                </div>
              </div>
              <div class="col-3">
                <el-switch v-model="nickname" size="large" disabled />

              </div>
            </div>
            <div class="row">
              <!-- Aboutme & switch -->
              <div class="col-9">
                <div class="input-group ">
                  <span class="input-group-text labelLength6">나의 관심사</span>
                  <textarea class="form-control" aria-label="With textarea" v-model="state.aboutme"></textarea>
                </div>
              </div>
              <div class="col-3">
                <el-switch v-model="swaboutme" size="large" class="align-middle" />
              </div>
            </div>
            <!-- Buttons -->
            <div style="margin:0.5em 0em;text-align: right ">
              <button type="button" class="btn btn-outline-primary btn-sm" @click="modify">정보 수정</button>
              <div class="spacer"></div>
              <button type="button" class="btn  btn-outline-dark btn-sm" data-bs-toggle="modal"
                data-bs-target="#exampleModal">계정 삭제</button>
            </div>
          </div>
          <br>
          <!-- User History -->
          <div class="col" style="background-color:;">
          </div>
        </div>
      </div>
      <!-- right column-->
      <div class="col-lg-6 col-sm-12">
        <PrivacySetting />
      </div>
    </div>
  </div>

  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">계정 삭제</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          정말로 계정을 삭제 하시겠습니까?
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">취소</button>
          <button type="button" class="btn btn-primary" data-bs-dismiss="modal" @click="deleteAccount">삭제하기</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
// import CircleListByUser from '../components/CircleListByUser.vue'
// import GoalListForPrivate from '../components/GoalListForPrivate.vue'
import { onMounted } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import { useRouter } from 'vue-router';
import axios from 'axios'
import PrivacySetting from '../components/PrivacySetting.vue';
import UserProfilePic from '../components/UserProfilePic.vue';
export default {
  data() {
    return {};
  },
  setup() {
    onMounted(() => { handleGetUser(); });
    const router = useRouter();
    let swaboutme = ref(null);
    let swemail = ref(null);
    const nicknamee = ref(null);
    let nickname = ref(true);
    const state = reactive({
      nickname: "",
      aboutme: "",
      studytopicPublic: "",
      emailPublic: "",
      imgName: "",
      path: "",
      uuid: "",
      img: '',
      urltest: '',
      email: sessionStorage.getItem("email"),
      token: sessionStorage.getItem("TOKEN"),
    });
    const handleGetUser = async () => {
      const url = "http://localhost:9090/api/userProfile";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = { email: state.email, };
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          state.nickname = res.data.dto.nickname;
          state.aboutme = res.data.dto.aboutme;
          swaboutme.value = res.data.dto.studytopicPublic;
          swemail.value = res.data.dto.emailPublic;
          state.urltest = res.data.dto.path;
          showImage(res.data.dto);
        } else {
          alert("somethings wrong");
        }
      });
    };
    const deleteAccount = async () => {
      const url = "http://localhost:9090/api/deleteAccount";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = { email: state.email, };
      await axios.post(url, body, { headers }).then(function (res) {
        if (res.status === 200) {
          alert(state.email + " 삭제 되었습니다");
          router.push({ name: "home" });
        }
      });
    };
    function sleep(sec) {
      return new Promise(resolve => setTimeout(resolve, sec * 1000));
    }
    const modify = async () => {
      modifyUser();
      await sleep(1);
      changePic();
      alert("개인정보가 변경되었습니다.")
      router.go(0);
    }
    const modifyUser = async () => {
      if (state.nickname === "") {
        alert("Please input nickname");
        nicknamee.value.focus();
        return false;
      }
      const url = "http://localhost:9090/api/userModify";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token,
      };
      const body = {
        user: state.email,
        nickname: state.nickname,
        aboutme: state.aboutme,
        emailPublic: swemail.value,
        studytopicPublic: swaboutme.value
      };
      const response = await axios.post(url, body, { headers });
      if (response.status === 200) {
        // alert("modify successfully");
        // router.go(0);
      }
      else {
        alert("fail");
        // router.go(0);
      }
    };

    //change profile pic start
    const changePic = async () => {
      let str = "";
      const liArr = document.querySelectorAll(".uploadResult ul li")

      const target = liArr[0];
      str += `
          <input type="hidden" name="imgName"
          value="${target.dataset.name}">
          <input type="hidden" name="path"
          value="${target.dataset.path}">
          <input type="hidden" name="uuid"
          value="${target.dataset.uuid}">
        `
      state.imgName = target.dataset.name;
      state.path = target.dataset.path;
      state.uuid = target.dataset.uuid;
      document.querySelector(".box").innerHTML = str
      const url = "http://localhost:9090/api/profilePicModify";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = {
        email: state.email,
        imgName: state.imgName,
        path: state.path,
        uuid: state.uuid,
      };
      const response = await axios.post(url, body, { headers });
      if (response.status === 200) {
        // alert("사진변경이 완료되었습니다");
        // router.go(0);
      }
      else {
        alert("사진변경에 실패했습니다.");
        // router.go(0);
      };
    };
    //change profile pic end`
    //show profilePic start
    const showImage = async (dto) => {
      const url = `http://localhost:9090/display`
      let str = "";
      str = `${url}?fileName=${dto.thumbnailURL}`;
      state.img = str;
    }
    //show profilePic end
    return { swaboutme, swemail, nickname, state, nicknamee, handleGetUser, deleteAccount, modifyUser, changePic, showImage, modify, sleep };
  },
  components: { PrivacySetting, UserProfilePic }
}
</script>
@import './assets/styles/yourstyles.css';
<style scoped>
.spacer {
  display: inline-block;
  width: 0.5em;
}

.stickleft {
  margin-left: 16em;
}

.labelLength6 {
  width: 6em;
}

.lowerAvatarPic {
  padding-top: 1em;
}

footer {
  bottom: 0;
  width: 100%;
  height: 8rem;
  /* Footer height */
}
input:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
textarea:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
</style>